<template>
  <div class="list">
    <div class="clearfix list-button">
      <div class="pull-left">
        <el-button   @click="createNew">创建新合同</el-button>
        <el-select v-model="stateValue" placeholder="请选择合同状态">
          <el-option
            v-for="item in stateOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
      </div>
    </div>
    <div class="list-center">
      <!--列表-->
      <el-table :data="tableData"  v-loading="listLoading" @selection-change="" class="chanceTable">
        <el-table-column type="index" label="编号" width="60" height="40" align="center">
        </el-table-column>
        <el-table-column prop="name" label="项目名称" align="center" >
        </el-table-column>
        <el-table-column prop="money" label="合同金额"  align="center" >
        </el-table-column>
        <el-table-column prop="customer" label="客户名称"  align="center" >
        </el-table-column>
        <el-table-column prop="contact" label="联系人" align="center"  >
        </el-table-column>
        <el-table-column prop="contactPhone" label="联系人手机号"  align="center"  >
        </el-table-column>
        <el-table-column prop="chanceTime" label="合同签署时间" align="center"  sortable>
        </el-table-column>
        <el-table-column prop="person" label="销售负责人"  align="center" >
        </el-table-column>
        <el-table-column prop="state" label="状态"  align="center" >
        </el-table-column>
        <el-table-column label="操作" width="180" align="center">
          <template scope="scope">
            <el-button size="small" @click="lookDetail(scope.$index, scope.row)">查看</el-button>
            <el-button type="danger" size="small" @click="edit(scope.$index, scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>

      <!--工具条-->
      <el-col :span="24" class="toolbar">
        <el-pagination background layout="prev, pager, next,total" @current-change="" :page-size="10" :total="total">
        </el-pagination>
      </el-col>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'chance',
    components: {},
    data () {
      return {
        stateOptions: [{
          value: 'state1',
          label: '全部'
        }, {
          value: 'state2',
          label: '未开始'
        }, {
          value: 'state3',
          label: '执行中'
        }, {
          value: 'state4',
          label: '已交付'
        }, {
          value: 'state5',
          label: '已关闭'
        }],
        stateValue: '',
        total: 7,
        page: 10,
        listLoading: false,
        tableData: [{
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '今日头条',
          person: '张三',
          state: '新线索'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '橙悦小程序',
          person: '李四',
          state: '跟进中'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '橙悦官网PC端',
          person: '王二麻子',
          state: '已终止'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '橙悦官网手机端',
          person: '张三',
          state: '全部'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '橙悦服务号图文',
          person: '李四',
          state: '新线索'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '丁香园',
          person: '王二麻子',
          state: '已终止'
        }, {
          name: 'xxxx开发项目',
          contact: '汪经理，等',
          money: '1,000,000',
          contactPhone: '138-0013-8000',
          chanceTime: '2017-11-25',
          customer: '解放号',
          person: '张三',
          state: '全部'
        }]
      }
    },
    watch: {
      /**
       * @params 监听参数变化重新获取数据
       * */
      params: {
        handler (val) {
          this.getData(val)
        },
        deep: true
      }
    },
    computed: {
      state () {
        return this.$store.state.app
      }
    },
    methods: {
      createNew () {
        this.$router.push({
          path: '/crm/contractDetail'
        })
      },
      /**
       * @params:category 分类 page：页码 limit:条数
       * */
      getData (params) {
      },
      /**
       * @on-change 页码改变的回调，返回改变后的页码
       * */
      pageChange (page) {
        this.params.page = page
      },
      /**
       * @on-page-size-change 切换每页条数时的回调，返回切换后的每页条数
       * */
      PageSizeChange (limit) {
        this.params.limit = limit
      },
      /**
       * 表格对应操作方法
       * @show 查看
       * @remove 删除
       * @edit
       */
      remove (index) {
        this.listData.splice(index, 1)
      },
      edit (index) {
      },
      /**
       * 多选
       * selection：已选项数据 row：刚选择的项数据
       */
      onSelect (selection, row) {
        // console.log(selection,row)
      },
      onSelectionChange (selection) {
        this.selection = selection
      },
      /**
       * 查看
       */
      lookDetail () {
        this.$router.push({
          path: '/crm/contractDetail'
        })
      }
    },
    created () {
      this.getData(this.params)
    },
    mounted () {
    }
  }
</script>



